



















import { Component, Prop, Vue } from 'vue-property-decorator';
import { Tab } from '../types/tab';
import WeirdCounter from './WeirdCounter.vue';
import BigButton from './input/BigButton.vue';
import { RawLocation } from 'vue-router';

@Component({
    name: 'TabFooter',
    components: {
        WeirdCounter,
        BigButton,
    },
})
export default class Footer extends Vue {
    @Prop() tab!: Tab;
    @Prop({default: true}) buttonEnabled!: boolean;

    route(location: RawLocation): void {
        this.$router.push(location);
    }

    handleButtonClicked() {
        this.$emit('buttonClicked');
    }
}
