            <h1 class="text-header" v-if="successor">
                Lorem ipsum sed <strong>{{ successor.name }}</strong>
            </h1>
            <section class="section-2" v-if="successor">
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec non ullamcorper felis.
                    Mauris efficitur nunc nibh, vitae auctor magna facilisis ac. Nullam egestas,
                    ex at varius laoreet, quam diam gravida massa, eu varius nulla
                    ipsum non ante. Vivamus nec.
                </p>
                <BigButton class="big-button" borderColor="primary" @click="route({
                    name: 'tabPage', params: { 'tabKey': successor.key }
                })">button_5.1</BigButton>
            </section>
            <div class="divider" />
            <InfoSection />
        </div>
        <TabFooter v-if="tab" :tab="tab" @buttonClicked="route({
                name: 'summaryPage',
                params: { 'tabKey': tab.key },
            })" />
        <PageNotFound v-if="loadError" :message="loadError" />
        <ContentLoader v-if="!tab" />
    </div>
</template>

<script>
    import MainPageHeader from '../components/MainPageHeader.vue';
    import PageNotFound from '../components/PageNotFound.vue';
    import ContentLoader from '../components/ContentLoader.vue';
    import TabFooter from '../components/TabFooter.vue';
    import BigButton from '../components/input/BigButton.vue';
    import InfoSection from '../components/InfoSection.vue';
    import { getTabService } from '../services/tabService';

    export default {
        name: 'TabPage',
        components: {
            MainPageHeader,
            PageNotFound,
            ContentLoader,
            TabFooter,
            BigButton,
            InfoSection,
        },

        data() {
            return {
                tab: null,
                successor: null,
                loadError: null,
            };
        },

        mounted() {
            (async () => {
                const tab = await getTabService().getTab(this.$route.params.tabKey);

                if (tab.successor) {
                    this.successor = await getTabService().getTab(tab.successor);
                }

                this.tab = tab;

                if (this.tab === null) {
                    this.loadError = `The tab with the specified key doesn't exist`;
                }
            })();
        },

        methods: {
            route(location) {
                this.$router.push(location);
            }
        },

        beforeRouteUpdate(to, from, next) {
            this.tab = null;
            this.loadError = null;

            (async () => {
                const tab = await getTabService().getTab(to.params.tabKey);

                if (tab.successor) {
                    this.successor = await getTabService().getTab(tab.successor);
                } else {
                    this.successor = null;
                }

                this.tab = tab;

                if (this.tab === null) {
                    this.loadError = `The tab with the specified key doesn't exist`;
                }

                next();
            })();
        }
    };
</script>

